import React, { Component } from "react";
import "../css/mainPage.css";
import {
  Card,
  CardImg,
  CardText,
  CardBody,
  CardTitle,
  Button
} from "reactstrap";

class Card2 extends Component {
  constructor(props) {
    super(props);
    this.handleClick = this.handleClick.bind(this);
  }
  handleClick(event) {
    const selectedId = event.target.id;
    console.log(selectedId);
    console.log(this);
    this.props.addItems(selectedId);
  }

  // handleMouseOn(event) {
  //   event.target.src = s.url2;
  // }

  render() {
    return this.props.stock.map(s => (
      <Card
        className="card2"
        style={{
          display: "inline-block",
          margin: "17px",
          left: "5%",
          right: "5%",
          textAlign: "center",
          width: "290px",
          height: "15%"
        }}
      >
        <CardImg
          width="100%"
          src={s.url}
          alt={s.name}
          onMouseEnter={e => (e.target.src = s.url2)}
          onMouseLeave={e => (e.target.src = s.url)}
        />
        <CardBody>
          <CardTitle>{s.name}</CardTitle>
          <CardText>$ {s.price}</CardText>
          <Button id={s.id} onClick={this.handleClick}>
            Add to Cart
          </Button>
        </CardBody>
      </Card>
    ));
  }
}

export default Card2;
