import React from "react";
import "../css/mainPage.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink
} from "reactstrap";
import { Link } from "react-router-dom";

export default class Navbar1 extends React.Component {
  constructor(props) {
    super(props);
    this.toggle = this.toggle.bind(this);
    this.state = {
      isOpen: false
    };
  }
  toggle() {
    this.setState({
      isOpen: !this.state.isOpen
    });
  }
  render() {
    return (
      <div>
        <Navbar color="white" light expand="lg">
          <NavbarBrand tag={Link} to="/">
            <img
              className="logo"
              src={require("../images/Logo.png")}
              alt="Align Logo"
            />
          </NavbarBrand>
          <NavbarToggler onClick={this.toggle} />
          <Collapse isOpen={this.state.isOpen} navbar>
            <Nav className="ml-auto" navbar>
              <NavItem>
                <NavLink tag={Link} to="/clothes">
                  Clothes
                </NavLink>
              </NavItem>
              <NavItem>
                <NavLink tag={Link} to="/accessories">
                  Accessories
                </NavLink>
              </NavItem>
              <NavItem>
                <NavLink tag={Link} to="/shoes">
                  Shoes
                </NavLink>
              </NavItem>
              <NavItem>
                <NavLink tag={Link} to="/clearance">
                  Clearance
                </NavLink>
              </NavItem>
              <NavItem>
                <NavLink tag={Link} to="/shopping-cart">
                  <FontAwesomeIcon
                    className="cart"
                    icon="shopping-cart"
                    size="lg"
                  />
                  ({this.props.itemCount})
                </NavLink>
              </NavItem>
            </Nav>
          </Collapse>
        </Navbar>
      </div>
    );
  }
}
