import React, { Component } from "react";
import Navbar from "../Navbar.js";
import Card2 from "../Card2.js";
import "../../css/mainPage.css";
import EventBanner from "../eventbanner.js";

class Shoes extends Component {
  constructor(props) {
    super(props);
    this.shoesStock = this.props.stock.filter(item => item.type === "shoes");
  }
  render() {
    return (
      <div>
        <Navbar itemCount={this.props.itemCount} />
        <EventBanner
          style={{ fontFamily: "PT Sans" }}
          promo={"Free shipping on U.S. orders over $50"}
        />
        <hr />
        <h2 style={{ textAlign: "center", fontFamily: "PT Sans" }}>
          Women's Shoes
        </h2>
        <Card2 stock={this.shoesStock} addItems={this.props.addItems} />
      </div>
    );
  }
}

export default Shoes;
